import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

function Write({ loginedUser }) {
    const navigate = useNavigate();
    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');

    const calcleClickHandler = () => {
        navigate('/notice-board/');
    };

    const submitClickHandler = async () => {
        if (!title || !content) {
            alert("   .");
            return;
        }

        try {
            const response = await axios.post('http://portfoliokjm.cafe24app.com/api/createBoard', {
                title,
                content,
                writerIndex: loginedUser.user.index // Assuming loginedUser contains user info with index
            });

            if (response.data.message === 'Board created successfully') {
                alert("  .");
                navigate('/notice-board/');
            }
        } catch (error) {
            console.error('Error creating board:', error);
            alert('    .');
        }
    };

    return (
        <div className='page write'>
            <div className='container'>
                <div className='writeWrap'>
                    <div className='item'>
                        <div className='key'>
                                                    
                        </div>
                        <div className='value'>
                            <input 
                                type="text"
                                value={title}
                                onChange={(e) => setTitle(e.target.value)}
                            />
                        </div>
                    </div>
                    <div className='item'>
                        <div className='key'>
                            
                        </div>
                        <div className='value'>
                            <textarea 
                                value={content}
                                onChange={(e) => setContent(e.target.value)}
                            />
                        </div>
                    </div>
                    <div className='btnWrap'>
                        <button type='button' className='submit' onClick={submitClickHandler}></button>
                        <button type='button' className='cancle' onClick={calcleClickHandler}></button>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Write;
