import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import cookie from 'react-cookies';

function Login() {
    const [id, setId] = useState('guest');
    const [pw, setPw] = useState('guest1234');
    const navigate = useNavigate();

    const loginPost = () => {
        if (id.trim() === '') {
            alert("  .");
            return;
        }
        if (pw.trim() === '') {
            alert("  .");
            return;
        }

        axios.post('http://portfoliokjm.cafe24app.com/api/noticeboard-login', { id, pw }, {
            headers: {
                'Content-Type': 'application/json'
            },
            withCredentials: true //      
        })
        .then(response => {
            //        
            const expires = new Date();
            expires.setMinutes(expires.getMinutes() + 60); //     (: 60 )

            //      response.data    .
            cookie.save('loginedUser', JSON.stringify(response.data), {
                path: '/notice-board/', //   
                expires, //    
                // secure: true, // HTTPS    ( )
                // httpOnly: true // HTTP     ( )
            });

            // alert(" .");
            navigate('/notice-board/');
        })
        .catch(error => {
            console.error(" :", error);
            alert("    .");
        });
    };

    return (
        <div className='page login'>
            <div className='container'>
                <div className='loginInner'>
                    <div className='left'>
                        <p className='text'>
                            Welcome to FreeBoard!
                        </p>
                    </div>
                    <div className='right'>
                        <div className='loginText'>
                            
                        </div>
                        <input
                            type="text"
                            placeholder='Id'
                            autoFocus
                            value={id}
                            onChange={(e) => setId(e.target.value)}
                        />
                        <input
                            type="password"
                            placeholder='Pw'
                            value={pw}
                            onChange={(e) => setPw(e.target.value)}
                        />
                        <button type='button' className='loginBtn' onClick={loginPost}>
                            LOGIN
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Login;
